<template>
	<div>
		<div v-if="this.peer == null">
			<button @click="connect">Connect</button>
		</div>
		<div v-else-if="this.connection == null">
			<span>Connecting...</span><br/>
			<button @click="cancelConnect">Cancel</button>
		</div>
		<div v-else>
			<div v-if="created">
				<p>Current Answer: "{{answer}}"</p>
				<button @click="update">Update</button><div style="margin-bottom: 20px;"/>
				<button @click="endRound">End Round</button><div style="margin-bottom: 20px;"/>
			</div>
			<div v-else-if="roundEnded">
				<button @click="update">Update</button><div style="margin-bottom: 20px;"/>
			</div>
			<div>
				<form>
					<input :disabled="roundEnded" v-model="guess.text" placeholder="Guess a letter"/>
					<button :disabled="roundEnded || guess.text.length == 0" @click="guessLetter">Guess</button>
				</form>
			</div>
			<button @click="disconnect">Disconnect</button>
		</div>
	</div>
</template>

<script>
const PacketTypes = {
	Answer: 0,
	Reset: 1,
	SetCreator: 2,
	EndRound: 3,
	GuessLetter: 4
}

import Peer from 'peerjs';
export default {
	data(){
		return {
			/** @type {Peer} */
			peer: null,
			/** @type {Peer.DataConnection} */
			connection: null,
			created: false,
			tempC: null,
			roundEnded: false,
			answer: "",
			guess: {
				text: ''
			}
		}
	},
	methods: {
		guessLetter(){
			this.connection.send(JSON.stringify({
				type: PacketTypes.GuessLetter,
				data: {
					letter: this.guess.text
				}
			}))
			this.guess.text = ''
		},
		update(){
			let v = prompt("Give me a word");
			if (v == null)
				return;
			
			if (v.trim().length == 0)
			{
				alert("Must be a valid word or phrase");
				return;
			}
			if (this.connection != null)
			{
				this.connection.send(JSON.stringify(
					{
						type: PacketTypes.Reset,
						data: {
							answer: v
						}
					}
				))
			}
		},
		endRound(){
			if (this.created)
			{
				if (this.connection != null)
				{
					this.connection.send(JSON.stringify(
						{
							type: PacketTypes.EndRound
						}
					))
				}
			}
		},
		disconnect(){
			if (this.tempC != null)
			{
				this.tempC.close();
			}
			if (this.peer != null)
			{
				this.peer.destroy();
			}
			this.$set(this, 'tempC', null)
			this.$set(this, 'connection', null)
			this.$set(this, 'peer', null)
			this.$forceUpdate();
		},
		cancelConnect(){
			this.disconnect();
		},
		connect() {
			let code = prompt("Enter room code");
			if (code == null)
				return;
			
			code = code.trim();
			let peer = new Peer();
			this.$set(this, 'peer', peer)
			
			let conn = peer.connect(code);
			this.$set(this, 'tempC', conn)
			conn.on('open', ()=>{
				this.$set(this, 'connection', conn)
				conn.on('data', (data)=>{
					let packet = JSON.parse(data);
					let type = packet.type;
					switch(type)
					{
						case PacketTypes.Answer:
							this.created = false;
							this.roundEnded = false;
							this.answer = packet.data.answer;
							break;
						case PacketTypes.Reset:
							this.created = false;
							this.roundEnded = false;
							break;
						case PacketTypes.SetCreator:
							this.roundEnded = false;
							this.created = packet.data.value;
							break;
						case PacketTypes.EndRound:
							this.roundEnded = true;
							break;
					}
				})
			})
			conn.on('error', ()=>{
				conn.close();
				this.$set(this, 'connection', null)
				alert('errored')
			})
			conn.on('close', ()=>{
				this.$set(this, 'connection', null)
				this.disconnect();
				alert('closed')
			})
		}
	},
	mounted(){
		this.connect();
	},
	beforeDestroy(){
		this.isDestroying = true;
		this.peer.destroy();
		this.$set(this, 'peer', null)
	}
}
</script>

