<template>
	<div class="hangman">
		<h2 v-if="this.peer != null && this.peer.id != null">Connection ID: {{this.peer.id}}</h2>
		<h2 v-else>Getting connection ID...</h2>
		<div>
			<img class="hangman-img" alt="manhang" :src="`hangmen/man${index}.png`">
			<div class="wordbox flex">
				<div class="flex-item" v-for="(v, key) in sorted_failures" :key="key">
					<span>
						{{v}}
					</span>
				</div>
			</div>
		</div>
		<p class="hangman-phrase">{{filtered_word}}</p>
		<div>
			<form>
				<input ref="hangmanInput" :disabled="has_won || has_lost" placeholder="Put a letter here" v-model="inputString"/>
				<button :disabled="has_won || has_lost || inputString.length == 0" @click="letter">{{submit_text}}</button>
			</form>
		</div>

		<button @click="fillInBlanks()">Finish Word</button><span style="margin-left: 20px;"/>
		<button @click="reset()">New Word</button>
	</div>
</template>

<script>
const PacketTypes = {
	Answer: 0,
	Reset: 1,
	SetCreator: 2,
	EndRound: 3,
	GuessLetter: 4
}


// @ is an alias to /src
import Peer from 'peerjs';
import { filter } from 'minimatch';
import { setTimeout } from 'timers';

export default {
	name: 'home',
	computed: {
		sorted_failures(){
			return this.failed_letters.sort();
		},
		submit_text(){
			if (this.inputString.length > 1)
			{
				return "Guess Phrase"
			}
			return "Submit Letter"
		},

		has_lost(){
			return this.index >= 9;
		},
		has_won(){
			for (var i = 0; i < this.word.length; ++i)
			{
				let char = this.word[i];
				// inefficient loop, but this is a game jam
				if (char == ' ')
				{
					continue;
				}
				else if (this.letters[char.toLowerCase()])
				{
					continue;
				}
				else
				{
					return false;
				}
			}
			return true;
		},
		index(){
			return Math.min(this.failed_letters.length, 9);
		},
		lower_word(){
			return this.word.toLowerCase().trim();
		},
		filtered_word(){
			let filtered = ""
			for (var i = 0; i < this.word.length; ++i)
			{
				let char = this.word[i];
				if (char == ' ')
				{
					filtered += ' ';
				}
				else if (this.letters[char.toLowerCase()])
				{
					filtered += this.word[i];
				}
				else
				{
					filtered += "_";
				}
			}
			return filtered;
		}
	},
	data(){
		return {
			isDestroying: false,
			/** @type {Peer} */
			peer: null,
			/** @type {Peer.DataConnection[]} */
			connections: [],
			damage: 0,
			letters: {},
			failed_letters: [],
			word: "Floop doop derp",
			inputString: ""
		}
	},
	watch: {
		has_won(){
			if (this.has_won)
			{
				for(var conn in this.connections)
				{
					this.connections[conn].send(JSON.stringify(
						{
							type: PacketTypes.EndRound
						}
					))
				}
			}
		},
		has_lost(){
			if (this.has_lost)
			{
				for(var conn in this.connections)
				{
					this.connections[conn].send(JSON.stringify(
						{
							type: PacketTypes.EndRound
						}
					))
				}
			}
		}
	},
	methods: {
		reset(value) {
			let v;
			if (value)
			{
				v = value;
			}
			else
			{
				v = prompt("Give me a word bitch")
			}
			
			if (v == null)
			{
				return;
			}

			this.damage = 0;
			this.$set(this, 'letters', {});
			this.word = "Floop doop derp";
			this.inputString = "";
			this.$set(this, "failed_letters", [])

			if (v != null && v != "" && v.trim() != "")
			{
				this.word = v;
				for(var conn in this.connections)
				{
					this.connections[conn].send(JSON.stringify(
						{
							type: PacketTypes.Answer,
							data: {
								answer: this.word
							}
						}
					))
				}
				for(var i = 0; i < this.word.length; ++i)
				{
					let char = this.word[i].toLowerCase();
					if (char.match(/[^A-Za-z ]/))
					{
						this.$set(this.letters, char, true);
					}
				}
				this.$refs.hangmanInput.focus();
			}
			else
			{
				this.reset();
			}
			setTimeout(() => {
				this.$refs.hangmanInput.focus();
			}, 0);
		},
		fillInBlanks(){
			for(var i = 0; i < this.word.length; ++i)
			{
				let char = this.word[i].toLowerCase();
				if (!char.match(/[^A-Za-z ]/))
				{
					this.$set(this.letters, char, true);
				}
			}
		},
		letter(real) {
			let str;
			if (real != null)
			{
				str = real.trim().toLowerCase();
			}
			else
			{
				str = this.inputString.trim().toLowerCase();
			}
			if (str.length == 0)
			{
				alert('You need to put in a letter, dumbass');
				return
			}
			else if (str.length > 1)
			{
				if (str == this.word.toLowerCase().trim())
				{
					for (var i = 0; i < this.word.length; ++i)
					{
						this.$set(this.letters, this.word[i].toLowerCase(), true)
					}
					setTimeout(()=>{
						alert("You have won!");
					}, 0)
					return;
				}
				setTimeout(()=>{
					alert("Your guess is incorrect");
				}, 0)
				return;
			}
			
			let char = str[0];
			if (char.match(/[^A-Za-z ]/))
			{
				alert('That isn\'t a letter, dumbass');
				this.$set(this, 'inputString', "");
				return;
			}
			if (this.letters[char])
			{
				alert('You\'ve already used this letter, dumbass')
				this.$set(this, 'inputString', "");
				return;
			}

			this.$set(this.letters, char, true);
			if (!this.lower_word.includes(char))
			{
				this.failed_letters.push(char);
			}

			if (this.has_won)
			{
				setTimeout(()=>{
					alert("You have won!");
				}, 0)
				return;
			}

			if (this.has_lost)
			{
				setTimeout(()=>{
					alert("You have lost!");
				}, 0)
				for (var i = 0; i < this.word.length; ++i)
				{
					this.$set(this.letters, this.word[i].toLowerCase(), true)
				}
				return;
			}

			this.$set(this, 'inputString', "");
		},
		initPeer(){
			//this.reset();
			this.$set(this, 'peer', new Peer());
			this.peer.on('open', ()=>{
				this.$forceUpdate();
			})
			this.peer.on('close', ()=>{
				if (this.peer != null && !this.isDestroying)
				{
					alert('Connection error, getting new connection ID...')
					let peer = this.peer;
					this.$set(this, 'peer', null)
					this.$set(this, 'connections', [])
					peer.destroy();
				}
				this.$forceUpdate();
			})
			this.peer.on('connection', (conn)=>{
				conn.on('open', ()=>{
					this.connections.push(conn);
					conn.send(JSON.stringify(
						{
							type: PacketTypes.Answer, data: {
								answer: this.word
							}
						}
					))
					
					if (this.connections.length == 1)
					{
						conn.send(JSON.stringify(
							{
								type: PacketTypes.SetCreator,
								data: {
									value: true
								}
							}
						))
					}

					if (this.has_lost || this.has_won)
					{
						conn.send(JSON.stringify(
							{
								type: PacketTypes.EndRound
							}
						))
					}

					let thisConn = conn;
					conn.on('data', (data)=>{
						let packet = JSON.parse(data);
						let type = packet.type;
						switch(type)
						{
							case PacketTypes.Reset:
							{
								this.reset(packet.data.answer);
								thisConn.send(JSON.stringify(
									{
										type: PacketTypes.SetCreator,
										data: {
											value: true
										}
									}
								))
								break;
							}
							case PacketTypes.EndRound:
							{
								this.fillInBlanks();
								for(var conn in this.connections)
								{
									this.connections[conn].send(JSON.stringify(
										{
											type: PacketTypes.EndRound
										}
									))
								}
								break;
							}
							case PacketTypes.GuessLetter:
							{
								this.letter(packet.data.letter);
								break;
							}
						}
					})
					conn.on('close', ()=>{
						let index = this.connections.indexOf(conn)
						if (index > -1)
						{
							this.connections.splice(index, 1);
						}
					})
				})
				conn.on('close', ()=>{
					let index = this.connections.indexOf(conn)
					if (index > -1)
					{
						this.connections.splice(index, 1);
					}
				})
			})
		}
	},
	mounted() {
		this.initPeer();
	},
	beforeDestroy(){
		this.isDestroying = true;
		this.peer.destroy();
		this.$set(this, 'peer', null)
	}
}
</script>

<style lang="scss" scoped>
.wordbox
{
	width: 30%;
}

.flex
{
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.flex-item
{
	background-color: rgba(255, 230, 230, 0.5);
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
	margin: 10px;
	font-size: 30px;
}

.hangman-img
{
	margin: auto;
	
	max-height: 40vh;
}

.hangman-phrase
{
	margin-left: auto;
	margin-right: auto;
	white-space: pre-wrap;
	font-size: 30px;
	letter-spacing: 10px;
	word-spacing: 50px;
}
</style>
